import {forEach} from "../../js/helpers";

document.addEventListener("DOMContentLoaded", function () {
	//  
	forEach(document.querySelectorAll('.compare-block__title'), (item, i) => {
		let compareContent = item.nextSibling;
		compareContent.style.maxHeight = compareContent.scrollHeight + 'px';

		item.addEventListener('click', () => {
			item.classList.toggle('collapsed');
			compareContent.classList.toggle('collapsed');
			if (compareContent.classList.contains('collapsed')) {
				compareContent.style.maxHeight = 0;
			} else {
				compareContent.style.maxHeight = compareContent.scrollHeight + 'px';
			}
		})
	});

	//     
	forEach(document.querySelectorAll('.compare-block__content'), (item, index) => {
		for (let i = 0; i < item.querySelectorAll('.compare-block__prop-title').length; i++) {
			let nthNumber = i + 1;
			let currentMaxRowHeight = 0;
			const rowElements = item.querySelectorAll('.compare-block__item:nth-of-type(' + nthNumber + ')');
			const rowTitle = item.querySelectorAll('.compare-block__prop-title')[i];

			forEach(rowElements, (item) => {
				if (item.offsetHeight > currentMaxRowHeight) {
					currentMaxRowHeight = item.offsetHeight;
				}
			});

			rowTitle.style.height = currentMaxRowHeight + 6 + 'px';
			forEach(rowElements, (item) => {
				item.style.height = currentMaxRowHeight + 'px'
			});
		}
	})
});