import {forEach} from "../../js/helpers";

forEach(document.querySelectorAll('.mainSearch'), mainSearchEl => {
  new bootstrap.Collapse(mainSearchEl, {
    toggle: false
  })
  // mainSearchEl.style.top = document.querySelector('.header').offsetHeight + 'px'
  const topEl = document.querySelector('.top')
  const searchList = document.querySelector('.searchList')
  const searchControl = mainSearchEl.querySelector('input[type="text"]')
  topEl.setAttribute('data-theme-color-default', topEl.getAttribute('data-theme-color'))
  mainSearchEl.addEventListener('hidden.bs.collapse', function () {
    document.body.classList.remove('is-search-active')
    topEl.setAttribute('data-theme-color', topEl.getAttribute('data-theme-color-default'))
    searchList.style.display = 'none'
  })
  mainSearchEl.addEventListener('show.bs.collapse', function () {
    document.body.classList.add('is-search-active')
    topEl.setAttribute('data-theme-color', 'dark')
    setTimeout(function () {
      searchControl.focus()
    }, 10)
  })
  document.addEventListener('click', e => {
    if(e.target.closest('.mainSearch')) return;
    searchList.style.display = 'none'
  })
  document.querySelector('[data-search-clear]').addEventListener('click', e => {
    searchControl.value = ''
    searchList.style.display = 'none'
  })
  searchControl.addEventListener('keyup', () => {
    if(searchControl.value.length) {
      mainSearchEl.classList.add('is-control-filled')
    } else {
      mainSearchEl.classList.remove('is-control-filled')
    }
  })
})
