import {Navigation, Swiper} from "swiper/js/swiper.esm";
import {break_xl_up} from "../../js/constants";
import {forEach} from "../../js/helpers";

Swiper.use([Navigation]);


document.addEventListener('tab-characteristic', () => {
  forEach(document.querySelectorAll('.item-character .swiper-container'), slider => {
    new Swiper(slider, {
        speed: 500,
        loop: false,
        slidesPerView: 1,
        navigation: {
          nextEl: slider.querySelector('.swiper-button-next'),
          prevEl: slider.querySelector('.swiper-button-prev'),
        },
        pagination: {
          el: slider.querySelector('.swiper-pagination'),
          type: 'bullets',
          clickable: true,
        },
        breakpoints: {
          [break_xl_up]: {
            slidesPerView: 2,
          },
        },
      }
    );
  });
})


