import {forEach} from "../../js/helpers";

(function () {
  const resetFormBtn = document.querySelector('.js_reset-review-form');
  if (resetFormBtn) {
    resetFormBtn.addEventListener('click', function (e) {
      e.preventDefault();

      const reviewFormFields = document.querySelectorAll('.review-write input, .review-write textarea, .review-write ~' +
          ' .b-login input, .review-write ~ .b-login textarea');
      forEach(reviewFormFields, item => {
        item.value = '';
      })
    });
  }
})();
