import mapCluster from '../../images/mapCluster.png'
import {forEach, filterByValue} from "../../js/helpers";

let deliveryYaMap;

//    
(function () {
	const deliveriesMap = document.querySelector('.js_deliveries-stage');
	const deliveriesMapSearch = document.querySelector('.js_delivery-search input');
	const deliveriesMapList = document.querySelector('.js_map-deliveries-list');
	if (typeof deliveries === 'undefined') return;
	let deliveriesData = deliveries.slice(0);
	let zoomMargin = (document.documentElement.clientWidth > 1040) ? [0, 445, 0, 0] : 0;

	if (deliveriesMap) {
		if (typeof ymaps === 'undefined') return;
		ymaps.ready(initMap);

		//  
		let deliveryCoords = deliveriesData.map(item => {
			return item.coords
		});

		function initMap() {
			deliveryYaMap = new ymaps.Map(deliveriesMap, {
				center: [55.751574, 37.573856],
				zoom: 9,
				controls: ['zoomControl'],
			}, {
				searchControlProvider: 'yandex#search'
			});

			let clusterer = new ymaps.Clusterer({
				clusterIcons: [
					{
						href: mapCluster,
						size: [60, 60],
						offset: [-30, -30],
					},
				],
				groupByCoordinates: false,
				clusterDisableClickZoom: false,
				clusterHideIconOnBalloonOpen: false,
				geoObjectHideIconOnBalloonOpen: false
			});
			let getPointData = function (index) {
				return {
					balloonContentBody: getDeliveryInfo(deliveriesData[index]),
					clusterCaption: deliveriesData[index].title
				};
			};
			let getPointOptions = function (index) {
						return {
							iconLayout: 'default#image',
							iconImageHref: deliveriesData[index].markerSrc,
							iconImageSize: [62, 70],
							iconImageOffset: [-62 / 2, -70],
						};
					},
					points = deliveryCoords,
					geoObjects = [];

			for (let i = 0, len = points.length; i < len; i++) {
				geoObjects[i] = new ymaps.Placemark(points[i], getPointData(i), getPointOptions(i));
				deliveriesMapList.insertAdjacentHTML('beforeend', getDeliveryPreview(deliveriesData[i], i));
			}

			if (geoObjects.length > 0) {
				clusterer.add(geoObjects);
				deliveryYaMap.geoObjects.add(clusterer);
				deliveryYaMap.setBounds(clusterer.getBounds(), {
					checkZoomRange: true,
					zoomMargin: zoomMargin
				});
			}

			forEach(document.querySelectorAll('.js_choose-delivery'), (btn) => {
				btn.addEventListener('click', function () {
					console.log('  - ' + btn.dataset.point);
					document.querySelector('.js_delivery-map').classList.remove('active');
				})
			});

			forEach(document.querySelectorAll('.commerce-info_preview'), (preview, index, array) => {
				preview.addEventListener('click', function () {
					forEach(array, item => {
						item.classList.remove('active');
					});

					preview.classList.add('active');

					deliveryYaMap.setCenter(deliveryCoords[preview.dataset.index], 14);

					//   ,      
					if (document.documentElement.clientWidth > 1040) {
						let pixelCenter = deliveryYaMap.getGlobalPixelCenter();
						pixelCenter = [
							pixelCenter[0] + 445,
							pixelCenter[1]
						];
						deliveryYaMap.setCenter(deliveryYaMap.options.get('projection').fromGlobalPixels(pixelCenter, deliveryYaMap.getZoom()));
					}

					geoObjects[preview.dataset.index].balloon.open();
				})
			});

			//      
			deliveriesMapSearch.addEventListener('input', function () {
				deliveriesData = filterByValue(deliveries, this.value);

				//  
				clusterer.removeAll();
				deliveryYaMap.geoObjects.removeAll();
				geoObjects = [];
				deliveriesMapList.innerHTML = '';

				deliveryCoords = deliveriesData.map(item => {
					return item.coords
				});

				for (let i = 0, len = deliveryCoords.length; i < len; i++) {
					geoObjects[i] = new ymaps.Placemark(deliveryCoords[i], getPointData(i), getPointOptions(i));
					deliveriesMapList.insertAdjacentHTML('beforeend', getDeliveryPreview(deliveriesData[i], i));
				}

				forEach(document.querySelectorAll('.js_choose-delivery'), (btn) => {
					btn.addEventListener('click', function () {
						console.log('  - ' + btn.dataset.point);
						document.querySelector('.js_delivery-map').classList.remove('active');
					})
				});

				forEach(document.querySelectorAll('.commerce-info_preview'), (preview, index, array) => {
					preview.addEventListener('click', function () {
						forEach(array, item => {
							item.classList.remove('active');
						});

						preview.classList.add('active');

						deliveryYaMap.setCenter(deliveryCoords[preview.dataset.index], 14);

						//   ,      
						if (document.documentElement.clientWidth > 1040) {
							let pixelCenter = deliveryYaMap.getGlobalPixelCenter();
							pixelCenter = [
								pixelCenter[0] + 445,
								pixelCenter[1]
							];
							deliveryYaMap.setCenter(deliveryYaMap.options.get('projection').fromGlobalPixels(pixelCenter, deliveryYaMap.getZoom()));
						}

						geoObjects[preview.dataset.index].balloon.open();
					})
				});

				if (geoObjects.length > 0) {
					clusterer.add(geoObjects);
					deliveryYaMap.geoObjects.add(clusterer);
					deliveryYaMap.setBounds(clusterer.getBounds(), {
						checkZoomRange: true,
						zoomMargin: zoomMargin
					});
				}
			});

			//     "   "
			deliveryYaMap.geoObjects.events
				.add('balloonopen', function () {
					forEach(document.querySelectorAll('.js_choose-delivery'), (btn) => {
						btn.addEventListener('click', function () {
							console.log('  - ' + btn.dataset.point);
							document.querySelector('.js_delivery-map').classList.remove('active');
						})
					});
			})
		}
	}
})();

function getDeliveryInfo(data) {
	//    
	let metroSection = '';
	let pointLogo = '';

	//   
	if (data.metro) {
		metroSection = data.metro.map(item => (
				`<div class="info-card__metroItem">
					<svg width="16" height="10" fill="${item.color}">
						<use xlink:href="#metro"></use>
					</svg>
					${item.title}
				</div>`
		)).join('')
	}

	if (data.logoSrc) {
		pointLogo += `<div class="info-card__point-logo"><img src="${data.logoSrc}" alt="${data.address}"></div>`
	}

	let deliveryInfoTemplate = `
		<div class="commerce-info commerce-info_delivery commerce-info_ymaps commerce-info_on-map">
			<div class="info-card section">
				<div class="info-card__inner">
					<div class="info-card__metro">
						${metroSection}
					</div>
					<div class="info-card__d-address">${(data.address) ? data.address : ''}</div>
					<div class="info-card__delivery">
						<span class="info-card__delivery-title">:</span>
						${(data.deliveryDate) ? data.deliveryDate : ''}${(data.price) ? ', ' + data.price : ''}
					</div>
					<div class="info-card__point">
						${pointLogo}
						<div class="info-card__point-info">
							${(data.info) ? data.info : ''}					
						</div>
					</div>
					<div class="info-card__payinfo">
						<img class="info-card__payinfo-icon" src="images/kit/delivery/modal/map-info-icon.png" alt="">
						<p class="info-card__payinfo-text">${(data.payInfo) ? data.payInfo : ''}</p>
					</div>
					<button class="info-card__choose-btn btn btn-dark btn-sm rounded-pill js_choose-delivery" data-point="${data.id}"> </button>
				</div>
			</div>
		</div>
	`;

	return deliveryInfoTemplate;
}
function getDeliveryPreview(data, index) {
	//    
	let metroSection = '';
	let pointLogo = '';

	//   
	if (data.metro) {
		metroSection = data.metro.map(item => (
				`<div class="info-card__metroItem">
					<svg width="16" height="10" fill="${item.color}">
						<use xlink:href="#metro"></use>
					</svg>
					${item.title}
				</div>`
		)).join('')
	}

	if (data.logoSrc) {
		pointLogo += `<div class="info-card__point-logo"><img src="${data.logoSrc}" alt="${data.address}"></div>`
	}

	let deliveryInfoTemplate = `
		<div class="commerce-info commerce-info_delivery commerce-info_preview commerce-info_on-map" data-index="${index}">
			<div class="info-card section">
				<div class="info-card__inner">
					<div class="info-card__metro">
						${metroSection}
					</div>
					<div class="info-card__d-address">${(data.address) ? data.address : ''}</div>
					<div class="info-card__delivery">
						<span class="info-card__delivery-title">:</span>
						${(data.deliveryDate) ? data.deliveryDate : ''}${(data.price) ? ', ' + data.price : ''}
					</div>
					<div class="info-card__point">
						${pointLogo}
						<div class="info-card__point-info">
							${(data.info) ? data.info : ''}					
						</div>
					</div>
					<div class="info-card__payinfo">
						<img class="info-card__payinfo-icon" src="images/kit/delivery/modal/map-info-icon.png" alt="">
						<p class="info-card__payinfo-text">${(data.payInfo) ? data.payInfo : ''}</p>
					</div>
					<button class="info-card__choose-btn btn btn-dark btn-sm rounded-pill js_choose-delivery" data-point="${data.id}"> </button>
				</div>
			</div>
		</div>
	`;

	return deliveryInfoTemplate;
}


//          
(function () {
	const deliveriesMap = document.querySelector('.js_delivery-map');
	const deliveriesMapList = document.querySelector('.delivery-modal__map-list');
	const viewSwitcher = document.querySelector('.js_delivery-view input');
	const deliveriesOpeners = document.querySelectorAll('.js_open-deliveries-map');

	if (deliveriesMap) {
		document.querySelector('.delivery-modal__map-close').addEventListener('click', function () {
			deliveriesMap.classList.remove('active');
		})
	}

	if (viewSwitcher) {
		viewSwitcher.addEventListener('change', function () {
			if (this.checked) {
				deliveriesMapList.classList.add('hidden');
			} else {
				deliveriesMapList.classList.remove('hidden');
			}
		})
	}

	if (deliveriesOpeners) {
		forEach(deliveriesOpeners, (btn) => {
			btn.addEventListener('click', function () {
				deliveriesMap.classList.add('active');
			})
		});
	}
})();