import {forEach} from "../../js/helpers";

forEach(document.querySelectorAll('[data-toggle="popup"]'), link => {
  const container = document.querySelector(link.getAttribute('data-trigger'))
  if (!container) return;
  link.addEventListener('click', () => {
    container.classList.add('is-active')
    link.classList.add('is-active')
    const scrollbarWidth = window.innerWidth - document.body.offsetWidth
    document.body.classList.add('popup-open')
    document.body.style.paddingRight = scrollbarWidth + 'px'
  })
  container.addEventListener('click', e => {
    if(e.target !== container) return;
    link.classList.remove('is-active')
    container.classList.remove('is-active')
    document.body.classList.remove('popup-open')
    document.body.style.paddingRight = ''
  })
  forEach(document.querySelectorAll('[data-dismiss="'+link.getAttribute('data-trigger').slice(1)+'"]'), close => {
    close.addEventListener('click', () => {
      container.classList.remove('is-active')
      document.body.classList.remove('popup-open')
    })
  })
})
