import {Swiper, Navigation} from "swiper/js/swiper.esm";
import {forEach} from "../../js/helpers";
import {break_lg_up, break_sm_up} from "../../js/constants";

Swiper.use([Navigation]);

(function () {
  const slider = document.querySelector('.itemsTwo__slider');
  if (!slider) return;
  new Swiper(slider, {
      speed: 500,
      loop: true,
      slidesPerView: 1,
      navigation: {
        nextEl: slider.querySelector('.swiper-button-next'),
        prevEl: slider.querySelector('.swiper-button-prev'),
      },
      pagination: {
        el: slider.querySelector('.swiper-pagination'),
        type: 'bullets',
        clickable: true,
      },
      breakpoints: {
        [break_lg_up]: {
          slidesPerView: 2,
        },
      },
    }
  );
})();
