import {forEach} from "../../js/helpers";

(function () {
	forEach(document.querySelectorAll('.js-stars-select'), stars => {
		let starsInput = stars.querySelector('.js_stars-field');
		let starsTogglers = stars.querySelectorAll('.js_star-select-item');

		forEach(starsTogglers, star => {
			star.addEventListener('click', function () {
				starsInput.value = star.getAttribute('data-value');

				forEach(starsTogglers, star => {
					star.classList.remove('selected');
					if (star.getAttribute('data-value') <= starsInput.value) {
						star.classList.add('selected');
					}
				})
			});

			star.addEventListener('mouseover', function () {
				let currentValue = star.getAttribute('data-value');
				forEach(starsTogglers, star => {
					star.classList.remove('selected');
					if (star.getAttribute('data-value') <= currentValue) {
						star.classList.add('selected');
					}
				})
			});

			star.addEventListener('mouseleave', function () {
				forEach(starsTogglers, star => {
					star.classList.remove('selected');
					if (star.getAttribute('data-value') <= starsInput.value) {
						star.classList.add('selected');
					}
				})
			})
		})
	})
})();
