import {Swiper, Pagination, Navigation, EffectFade, Autoplay} from "swiper/js/swiper.esm";
import {forEach, Move} from "../../js/helpers";
import {break_xl_up, break_md_up, break_xxl_up, break_xl_down, break_lg_up} from "../../js/constants";

new Move(document.querySelector('[data-move="productList-nav-get"]'), document.querySelector('[data-move="productList-nav-set"]'), break_xl_down).init();

Swiper.use([Pagination, Navigation, EffectFade, Autoplay]);

(function () {
  const sliderMain = document.querySelector('.productListSlider__inner');
  if (!sliderMain) return;
  const wrapper = sliderMain.parentNode.parentNode;
  const pagination = wrapper.querySelector('.swiper-pagination');
  // const delay = 5000;
  // pagination.style.setProperty('--delay', delay + 'ms');
  new Swiper(sliderMain, {
      speed: 500,
      slidesPerView: 1.15,
      slidesPerGroup: 1,
      loop: true,
      // effect: window.innerWidth >= break_md_up ? 'fade' :'slide',
      pagination: {
        el: pagination,
        type: 'bullets',
        clickable: true,
      },
      navigation: {
        nextEl: wrapper.querySelector('.swiper-button-next'),
        prevEl: wrapper.querySelector('.swiper-button-prev'),
      },
      breakpoints: {
        [break_md_up]: {
          slidesPerView: 3,
          slidesPerGroup: 3,
        },
        [break_xxl_up]: {
          slidesPerView: 4.15,
          slidesPerGroup: 4,
        },

      },
    }
  );
})();
