import {break_xl_up} from "../../js/constants";

document.addEventListener('tab-lookbook', () => {
  if (window.innerWidth >= break_xl_up) {
    const msnry = new Masonry( '.b-lookbook', {
      columnWidth: '.grid-sizer',
      gutter: '.gutter-sizer',
      itemSelector: '.grid-item',
      percentPosition: true
    });
  }
})
