import {forEach, nextAll} from "../../js/helpers";

const hiddenClass = 'd-none';

forEach(document.querySelectorAll('.tags'), $container => {
  const $toggle = $container.querySelector('.tags__toggle');
  if (!$toggle) return;
  const $items = $container.children;
  hideItems($container, $items, $toggle);
  $toggle.addEventListener('click', () => {
    if ($container.classList.contains("is-active")) {
      hideItems($container, $items, $toggle)
    } else {
      $container.classList.add("is-active");
      forEach($items, $item => $item.classList.remove(hiddenClass));
      $container.append($toggle);
    }
  })
});

function hideItems($container, $items, $toggle) {
  $container.classList.remove("is-active");
  setTimeout(function() {
    let lastElement;
    forEach($items, $item => {
      if (lastElement && lastElement.offsetTop > 1) {
        console.log(lastElement.offsetTop);
        $container.insertBefore($toggle, lastElement.previousElementSibling.previousElementSibling);
        forEach(nextAll($toggle), $item => {
          $item.classList.add(hiddenClass);
        });
        $toggle.classList.remove(hiddenClass);
        return false;
      }
      lastElement = $item;
    });
  }, 100);
}

function getIndex(el) {
  if (!el) return -1;
  var i = 0;
  do {
    i++;
  } while (el = el.previousElementSibling);
  return i;
}

