import {forEach} from "../../js/helpers";
import {Swiper} from "swiper/js/swiper.esm";
import {Move} from "../../js/helpers";
import {break_lg_down} from "../../js/constants";

new Move(document.querySelector('[data-move="recommend-single-get"]'), document.querySelector('[data-move="recommend-single-set"]'), break_lg_down).init();

forEach(document.querySelectorAll('.recommend-single__slider'), slider => {
  new Swiper(slider, {
    loop: true,
    navigation: {
      nextEl: slider.querySelector('.swiper-button-next'),
      prevEl: slider.querySelector('.swiper-button-prev'),
    },
    pagination: {
      el: slider.querySelector('.swiper-pagination'),
      type: 'bullets',
      clickable: true,
    },
    breakpoints: {},
  })
});
