import mapMarker from '../../images/mapMarker.png'
import mapCluster from '../../images/mapCluster.png'
import {forEach, filterByValue} from "../../js/helpers";

let shopsYaMap;

//    
(function () {
	const shopsMap = document.querySelector('.js_cities-stage');
	const shopsMapSearch = document.querySelector('.js_map-search input');
	const shopsMapList = document.querySelector('.js_map-shops-list');
	if (typeof shops === 'undefined') return;
	let shopsData = shops.slice(0);

	if (shopsMap) {
		if (typeof ymaps === 'undefined') return;
		ymaps.ready(initMap);

		//  
		let shopsCoords = shopsData.map(item => {
			return item.coords
		});

		function initMap() {
			shopsYaMap = new ymaps.Map(shopsMap, {
				center: [55.751574, 37.573856],
				zoom: 9,
				controls: ['zoomControl'],
			}, {
				searchControlProvider: 'yandex#search'
			});

			let clusterer = new ymaps.Clusterer({
				clusterIcons: [
					{
						href: mapCluster,
						size: [60, 60],
						offset: [-30, -30],
					},
				],
				groupByCoordinates: false,
				clusterDisableClickZoom: false,
				clusterHideIconOnBalloonOpen: false,
				geoObjectHideIconOnBalloonOpen: false
			});
			let getPointData = function (index) {
				return {
					balloonContentBody: getShopInfo(shopsData[index]),
					clusterCaption: shopsData[index].title
				};
			};
			let getPointOptions = function () {
						return {
							iconLayout: 'default#image',
							iconImageHref: mapMarker,
							iconImageSize: [50, 65],
							iconImageOffset: [-50 / 2, -65],
						};
					},
					points = shopsCoords,
					geoObjects = [];

			for (let i = 0, len = points.length; i < len; i++) {
				geoObjects[i] = new ymaps.Placemark(points[i], getPointData(i), getPointOptions());
				shopsMapList.insertAdjacentHTML('beforeend', getShopPreview(shopsData[i], i));
			}

			clusterer.add(geoObjects);
			shopsYaMap.geoObjects.add(clusterer);

			shopsYaMap.setBounds(clusterer.getBounds(), {
				checkZoomRange: true
			});

			forEach(document.querySelectorAll('.commerce-info_preview'), (preview, index, array) => {
				preview.addEventListener('click', function () {
					forEach(array, item => {
						item.classList.remove('active');
					});

					preview.classList.add('active');

					shopsYaMap.setCenter(shopsCoords[preview.dataset.index], 14);
					geoObjects[preview.dataset.index].balloon.open();
				})
			});

			//      
			shopsMapSearch.addEventListener('input', function () {
				shopsData = filterByValue(shops, this.value);

				//  
				clusterer.removeAll();
				shopsYaMap.geoObjects.removeAll();
				geoObjects = [];
				shopsMapList.innerHTML = '';

				shopsCoords = shopsData.map(item => {
					return item.coords
				});

				for (let i = 0, len = shopsCoords.length; i < len; i++) {
					geoObjects[i] = new ymaps.Placemark(shopsCoords[i], getPointData(i), getPointOptions());
					shopsMapList.insertAdjacentHTML('beforeend', getShopPreview(shopsData[i], i));
				}

				forEach(document.querySelectorAll('.commerce-info_preview'), (preview, index, array) => {
					preview.addEventListener('click', function () {
						forEach(array, item => {
							item.classList.remove('active');
						});

						preview.classList.add('active');

						shopsYaMap.setCenter(shopsCoords[preview.dataset.index], 14);
						geoObjects[preview.dataset.index].balloon.open();
					})
				});

				clusterer.add(geoObjects);
				shopsYaMap.geoObjects.add(clusterer);
				shopsYaMap.setBounds(clusterer.getBounds(), {
					checkZoomRange: true
				});
			});
		}
	}
})();

function getShopInfo(data) {
	//    
	let socialSection = '';
	let metroSection = '';
	//   
	data.socials.forEach(item => {
		if (item.name === 'whatsApp') {
			socialSection += `
				<a class="social__whatsapp" href="${item.value}">
					<svg>
						<use xlink:href="#whatsapp"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'viber') {
			socialSection += `
				<a class="social__viber" href="${item.value}">
					<svg>
						<use xlink:href="#viber"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'telegram') {
			socialSection += `
				<a class="social__telegram" href="${item.value}">
					<svg>
						<use xlink:href="#telegram"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'video') {
			socialSection += `
				<div class="social__video-link">
					<a class="video-link" href="${item.value}">
						<span class="video-link__icon"><svg><use xlink:href="#video"></use></svg></span>
						<span></span>
					</a>
				</div>
			`
		}
	});
	//   
	if (data.metro) {
		metroSection = data.metro.map(item => (
				`<div class="info-card__metroItem">
					<svg width="16" height="10" fill="${item.color}">
						<use xlink:href="#metro"></use>
					</svg>
					${item.title}
				</div>`
		)).join('')
	}

	let shopInfoTemplate = `
		<div class="commerce-info commerce-info_ymaps commerce-info_on-map">
			<div class="info-card section">
				<div class="info-card__inner">
					<div class="info-card__cover">
						${(data.image) ? `<img src="${data.image}">` : ''}
					</div>
					<a class="info-card__title" href="#">${(data.title) ? data.title : ''}</a>
					<div class="info-card__metro">
						${metroSection}
					</div>
					<div class="info-card__address">${(data.address) ? data.address : ''}</div>
					<div class="info-card__contact">${(data.contact) ? data.contact : ''}</div>
					<div class="info-card__days">
						${data.days} 
					</div>
					<div class="social commerce-info__social">
						${socialSection}						
					</div>
				</div>
			</div>
		</div>
	`;

	return shopInfoTemplate;
}
function getShopPreview(data, index) {
	//    
	let socialSection = '';
	let metroSection = '';
	//   
	data.socials.forEach(item => {
		if (item.name === 'whatsApp') {
			socialSection += `
				<a class="social__whatsapp" href="${item.value}">
					<svg>
						<use xlink:href="#whatsapp"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'viber') {
			socialSection += `
				<a class="social__viber" href="${item.value}">
					<svg>
						<use xlink:href="#viber"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'telegram') {
			socialSection += `
				<a class="social__telegram" href="${item.value}">
					<svg>
						<use xlink:href="#telegram"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'video') {
			socialSection += `
				<div class="social__video-link">
					<a class="video-link" href="${item.value}">
						<span class="video-link__icon"><svg><use xlink:href="#video"></use></svg></span>
						<span></span>
					</a>
				</div>
			`
		}
	});
	//   
	if (data.metro) {
		metroSection = data.metro.map(item => (
				`<div class="info-card__metroItem">
					<svg width="16" height="10" fill="${item.color}">
						<use xlink:href="#metro"></use>
					</svg>
					${item.title}
				</div>`
		)).join('')
	}

	let shopInfoTemplate = `
		<div class="commerce-info commerce-info_preview commerce-info_on-map" data-index="${index}">
			<div class="info-card section">
				<div class="info-card__inner">
					<div class="info-card__sity">
						<svg width="10" height="12">
							<use xlink:href="#location"></use>
						</svg>
						${(data.sity) ? data.sity : ''}
					</div>
					<a class="info-card__title" href="javascript:void(0);">${(data.title) ? data.title : ''}</a>
					<div class="info-card__metro">
						${metroSection}
					</div>
					<div class="info-card__address">${(data.address) ? data.address : ''}</div>
					<div class="info-card__contact">${(data.contact) ? data.contact : ''}</div>
					<div class="info-card__days">
						${data.days} 
					</div>
				</div>
			</div>
		</div>
	`;

	return shopInfoTemplate;
}


//          
(function () {
	const shopsList = document.querySelector('.js_shop-list');
	const shopsMap = document.querySelector('.js_cities-map');
	const viewSwitcher = document.querySelector('.js_shops-view input');

	if (viewSwitcher && shopsList) {
		let shopsListHeight = shopsList.offsetHeight;

		viewSwitcher.addEventListener('change', function () {
			if (this.checked) {
				shopsList.classList.remove('active');
				shopsList.style.height = 0 + 'px';

				shopsMap.classList.add('active')
			} else {
				shopsList.classList.add('active');
				shopsList.style.height = shopsListHeight + 'px';

				shopsMap.classList.remove('active')
			}
		});

		document.querySelector('.js_enable-mobile-map').addEventListener('click', function () {
			shopsMap.classList.add('active')
		});

		document.querySelector('.shops__cities-map-close').addEventListener('click', function () {
			shopsMap.classList.remove('active');
		})
	}
})();