//    
import {forEach} from "../../js/helpers";
import mapMarker from '../../images/mapMarker.png'
const shopDetailsMap = document.querySelector('.js_shop-details-map');

(function () {
	if (shopDetailsMap) {
		if (typeof ymaps === 'undefined') return;
		ymaps.ready(init);

		function init() {
			let shopMap = new ymaps.Map(shopDetailsMap, {
				center: [55.851015, 37.580589],
				zoom: 16,
				// controls: [],
			});

			forEach(document.querySelectorAll('.js_show-on-map'), shopOnMapBtn => {
				shopOnMapBtn.addEventListener('click', function () {
					if (shopDetailsMap) {
						if (typeof shops === 'undefined') return;

						const shopId = shopOnMapBtn.getAttribute('data-shop-id');
						const shopData = shops.find(obj => {
							return obj.id === shopId
						});

						shopDetailsMap.classList.add('active');
						const myPlacemark = new ymaps.Placemark(shopData.coords, {}, {
							iconLayout: 'default#image',
							iconImageHref: mapMarker,
							iconImageSize: [50, 65],
							iconImageOffset: [-50 / 2, -65],
						});

						shopMap.geoObjects.add(myPlacemark);
						shopMap.setCenter(shopData.coords);

						renderShopTemplate(shopDetailsMap.querySelector('.shops__details-info'), shopData)
					}
				});
			});

			//    
			shopDetailsMap.querySelector('.shops__details-map-close').addEventListener('click', function () {
				shopDetailsMap.classList.remove('active');
				shopMap.geoObjects.removeAll();
				shopDetailsMap.querySelector('.shops__details-info').innerHTML = '';
				if (shopDetailsMap.querySelector('.shops__details-map-content').classList.contains('expanded')) {
					shopDetailsMap.querySelector('.shops__details-map-content').classList.remove('expanded')
				}
			});

			//     
			shopDetailsMap.querySelector('.js_detail-expand').addEventListener('click', function () {
				shopDetailsMap.querySelector('.shops__details-map-content').classList.toggle('expanded');
			})
		}
	}
})();

//        
function renderShopTemplate(element, data = Array) {
	let socialSection = '';
	let metroSection = '';

	//   
	data.socials.forEach(item => {
		if (item.name === 'whatsApp') {
			socialSection += `
				<a class="social__whatsapp" href="${item.value}">
					<svg>
						<use xlink:href="#whatsapp"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'viber') {
			socialSection += `
				<a class="social__viber" href="${item.value}">
					<svg>
						<use xlink:href="#viber"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'telegram') {
			socialSection += `
				<a class="social__telegram" href="${item.value}">
					<svg>
						<use xlink:href="#telegram"></use>
					</svg>
				</a>
			`
		} else if (item.name === 'video') {
			socialSection += `
				<div class="social__video-link">
					<a class="video-link" href="${item.value}">
						<span class="video-link__icon"><svg><use xlink:href="#video"></use></svg></span>
						<span></span>
					</a>
				</div>
			`
		}
	});

	//   
	if (data.metro) {
		metroSection = data.metro.map(item => (
				`<div class="info-card__metroItem">
					<svg width="16" height="10" fill="${item.color}">
						<use xlink:href="#metro"></use>
					</svg>
					${item.title}
				</div>`
		)).join('')
	}

	let shopInfoTemplate = `
		<div class="commerce-info commerce-info_on-map">
			<div class="info-card section">
				<div class="info-card__inner">
					<div class="info-card__sity">
						<svg width="10" height="12">
							<use xlink:href="#location"></use>
						</svg>
						${(data.sity) ? data.sity : ''}
					</div>
					<a class="info-card__title" href="#">${(data.title) ? data.title : ''}</a>
					<div class="info-card__metro">
						${metroSection}
					</div>
					<div class="info-card__address">${(data.address) ? data.address : ''}</div>
					<div class="info-card__contact">${(data.contact) ? data.contact : ''}</div>
					<div class="social commerce-info__social">
						${socialSection}						
					</div>
					<div class="info-card__days">
						<span>  :</span>
						${data.days} 
					</div>
					<div class="info-card__how-get">
						${(data.howGet) ? data.howGet : ''} 
					</div>
				</div>
			</div>
		</div>
	`;

	element.innerHTML = shopInfoTemplate;
}