forEach(document.querySelectorAll('[data-toggle="select-toggle"]'), select => {
  forEach(select.options, option => {
    const collapse = document.querySelectorAll(select.dataset.target + '-' + option.value);
    forEach(collapse, content => {
      content.style.setProperty('--max-height', content.scrollHeight + 'px');
    });
  });
  const updateCollapse = () => {
    forEach(select.options, option => {
      const collapse = document.querySelectorAll(select.dataset.target + '-' + option.value);
      forEach(collapse, content => {
        content.setAttribute('aria-expanded', select.options[select.selectedIndex].value == option.value ? "true" : "false");
      });
    });
  }
  select.addEventListener('change', () => {
    updateCollapse()
  });
  select.addEventListener('select-change', () => {
    updateCollapse()
  });
});

forEach(document.querySelectorAll('.ui-slider-wrapper'), wrapper => {
  const slider = wrapper.querySelector('#ui-slider')
  const from = wrapper.querySelector('.ui-slider-from')
  const to = wrapper.querySelector('.ui-slider-to')

  noUiSlider.create(slider, {
    start: [+slider.dataset.from, +slider.dataset.to],
    connect: true,
    range: {
      'min': +slider.dataset.min,
      'max': +slider.dataset.max
    }
  });
  slider.noUiSlider.on('update', function (values, handle) {
    const value = values[handle];
    if (handle) {
      to.textContent = Math.round(value);
    } else {
      from.textContent = Math.round(value);
    }
  });
});

forEach(document.querySelectorAll('textarea.autogrow'), textarea => {
  textarea.addEventListener('input', () => {
    textarea.style.height = "5px";
    textarea.style.height = (textarea.scrollHeight)+"px";
  })
});


forEach(document.querySelectorAll('.flatpickr-order'), item => {
  flatpickr(item, {
    inline: true,
    locale: "ru"
  });
})


forEach(document.querySelectorAll('.form-order-switch'), conteiner => {
  const control = conteiner.querySelector('input')
  const buttons = conteiner.querySelectorAll('[data-price]')
  control.addEventListener('change', () => {
    forEach(buttons, button => button.classList.remove('is-active'))
    if (control.checked) {
      buttons[1].classList.add('is-active')
    } else {
      buttons[0].classList.add('is-active')
    }
  })
  forEach(buttons, button => {
    button.addEventListener('click', () => {
      button.classList.add('is-active')
      if (button === buttons[0]) {
        control.checked = false
        buttons[1].classList.remove('is-active')
      } else {
        buttons[0].classList.remove('is-active')
        control.checked = true
      }
    })
  })
})


