var tooltipTriggerList = [].slice.call(document.querySelectorAll('[data-toggle="tooltip"]'))
var tooltipList = tooltipTriggerList.map(function (tooltipTriggerEl) {
  const tooltip = new bootstrap.Tooltip(tooltipTriggerEl)
  const color = getComputedStyle(tooltipTriggerEl).getPropertyValue('--color-hover')
  if (tooltipTriggerEl.dataset.color === 'custom') {
    tooltipTriggerEl.addEventListener('inserted.bs.tooltip', function () {
      tooltip.tip.style.setProperty('--color', color)
      tooltip.tip.classList.add('has-custom-color')
    })
  }
  return tooltip
})

Swiper.use([Pagination, Navigation, Scrollbar]);

(function () {
  if (window.innerWidth <= break_xl_down) {
    const sliderEl = document.querySelector('.slider-tabs-titles');
    if (!sliderEl) return;
    const Slider = new Swiper(sliderEl, {
        slidesPerView: 'auto',
        slidesPerGroup: 2,
        scrollbar: {
          el: '.swiper-scrollbar',
          // draggable: true,
        },
      }
    );
    forEach(sliderEl.querySelectorAll('.nav-item'), link => {
      link.addEventListener('click', e => {
        Slider.slideTo(Slider.clickedIndex);
      })
    })
  }
})();



(function () {
  if (window.innerWidth <= break_xl_down) {
    const slider = document.querySelector('.block-img-slider');
    if (!slider) return;
    new Swiper(slider, {
        slidesPerView: 1,
        pagination: {
          el: slider.querySelector('.swiper-pagination'),
          type: 'bullets',
          clickable: true,
        },
        breakpoints: {
          [break_xl_up]: {
            slidesPerView: 2,
            slidesPerGroup: 2,
          },
        },
      }
    );
  }
})();

(function () {
  forEach(document.querySelectorAll('.page-slider'), slider => {
    new Swiper(slider, {
        slidesPerView: 1,
        navigation: {
          nextEl: slider.querySelector('.swiper-button-next'),
          prevEl: slider.querySelector('.swiper-button-prev'),
        },
        pagination: {
          el: slider.querySelector('.swiper-pagination'),
          type: 'bullets',
          clickable: true,
        },
      }
    );
  })
})();

var triggerTabList = [].slice.call(document.querySelectorAll('.b-block-check input'))
triggerTabList.forEach(function (triggerEl) {
  var tabTrigger = new bootstrap.Tab(triggerEl)

  triggerEl.addEventListener('change', function (e) {
    e.preventDefault()
    tabTrigger.show()
  })
})


new Move(document.querySelector('[data-move="order-submit-get"]'), document.querySelector('[data-move="order-submit-set"]'), break_xl_down).init();
new Move(document.querySelector('[data-move="page-menu-get"]'), document.querySelector('[data-move="page-menu-set"]'), break_xl_down).init();

const setColumnWidth = () => {
  const root = document.documentElement
  const elem = document.querySelector('.page-content-column')
  if (!elem) return;
  const columnWidth = elem.offsetWidth
  root.style.setProperty('--columnWidth', columnWidth + 'px')
}
setColumnWidth()


forEach(document.querySelectorAll('[data-equal-window-height]'), wrapper => {
  wrapper.style.height = window.innerHeight + 'px'
})

forEach(document.querySelectorAll('.dropdown-stop-close'), dropdown => {
  dropdown.addEventListener('click', e => {
    e.stopPropagation()
  })
})


import mapMarker from './images/mapMarker.svg'
forEach(document.querySelectorAll('.contact-map'), el => {
  if (typeof ymaps === 'undefined') return;
  ymaps.ready(init);

  function init() {
    const myMap = new ymaps.Map(el, {
      center: [55.851015, 37.580589],
      zoom: 16,
      // controls: [],
    });
    myMap.behaviors.disable('scrollZoom');
    const myPlacemark = new ymaps.Placemark(myMap.getCenter(), {}, {
      iconLayout: 'default#image',
      iconImageHref: mapMarker,
      iconImageSize: [100, 100],
      iconImageOffset: [-100/2, -100]
    });

    myMap.geoObjects.add(myPlacemark)
  }
})


