import {Swiper, Pagination, Navigation} from "swiper/js/swiper.esm";
import {break_md_up, break_lg_up} from "../../js/constants";

Swiper.use([Pagination, Navigation]);

(function () {
  const slider = document.querySelector('.b-insta__slider');
  if (!slider) return;
  const wrapper = slider.closest('.section');
  new Swiper(slider, {
      speed: 500,
      loop: true,
      slidesPerView: 1.5,
      pagination: {
        el: wrapper.querySelector('.swiper-pagination'),
        type: 'bullets',
        clickable: true,
      },
      navigation: {
        nextEl: wrapper.querySelector('.swiper-button-next'),
        prevEl: wrapper.querySelector('.swiper-button-prev'),
      },
      breakpoints: {
        [break_md_up]: {
          slidesPerView: 2.35,
        },
        [break_lg_up]: {
          slidesPerView: 4.35,
        },
      },
    }
  );
})();
