import {forEach} from "../../js/helpers";
import {Swiper, Pagination, Navigation, EffectFade, Autoplay} from "swiper/js/swiper.esm";
import {break_md_up, break_xl_up} from "../../js/constants";

Swiper.use([Pagination, Navigation, EffectFade]);



forEach(document.querySelectorAll('.product-media'), item => {
  const sliderElem = item.querySelector('.sliderProduct .swiper-container')
  const thumbsElem = item.querySelector('.sliderProductThumbs .swiper-container')
  const modalProductEl = document.getElementById('modal-product')
  const parentEl = item.parentNode
  const zoomEl = item.querySelector('.sliderProduct__zoom')
  const modalProduct = new bootstrap.Modal(modalProductEl)

  zoomEl.addEventListener('click', () => {
    if (sliderElem.closest('.modal')) {
      modalProduct.hide()
    } else {
      modalProduct.show()
    }
  })

  sliderElem.addEventListener('click', () => {
    if (sliderElem.closest('.modal')) {
      modalProduct.hide()
    } else {
      modalProduct.show()
    }
  })


  setTimeout(() => {
    const thumbs = new Swiper(thumbsElem, {
      slidesPerView: 3.3,
      watchSlidesVisibility: true,
      watchSlidesProgress: true,
      breakpoints: {
        [break_md_up]: {
          slidesPerView: 5,
        },
        [break_xl_up]: {
          slidesPerView: 3,
        },
      },
    });
    const slider = new Swiper(sliderElem, {
      effect: window.innerWidth >= break_md_up ? 'fade' :'slide',
      navigation: {
        nextEl: item.querySelector('.swiper-button-next'),
        prevEl: item.querySelector('.swiper-button-prev'),
      },
      pagination: {
        el: item.querySelector('.swiper-pagination'),
        type: 'fraction',
        clickable: true,
      },
      breakpoints: {},
      thumbs: {
        swiper: thumbs
      }
    })

    modalProductEl.addEventListener('show.bs.modal', function (event) {
      modalProductEl.querySelector('.modal-body').appendChild(item)
      modalProductEl.classList.add('is-loading')
      document.body.classList.add('is-product-modal-open')
    })
    modalProductEl.addEventListener('shown.bs.modal', function (event) {
      window.dispatchEvent(new Event('resize'));
      modalProductEl.classList.remove('is-loading')
      item.style.setProperty('--thumbs-height', thumbsElem.scrollHeight + 'px');
    })
    modalProductEl.addEventListener('hide.bs.modal', function (event) {
      parentEl.appendChild(item)
    })
    modalProductEl.addEventListener('hidden.bs.modal', function (event) {
      document.body.classList.remove('is-product-modal-open')
      window.dispatchEvent(new Event('resize'));
    })
  }, 2000)

});


forEach(document.querySelectorAll('#myTab .nav-link'), link => {
  link.addEventListener('click', () => {
    setTimeout(function () {
      switch (link.getAttribute('href')) {
        case '#':
          document.dispatchEvent(new CustomEvent("tab-characteristic"))
          break
        case '#Lookbook':
          document.dispatchEvent(new CustomEvent("tab-lookbook"))
          break
      }
    }, 300)
  })
})
