import {setCookie, getCookie, deleteCookie, forEach} from "../../js/helpers";

(function () {
	let askCityModal = document.querySelectorAll('.ask-city-wrap');

	if (getCookie('location') === undefined && askCityModal.length > 0) {
		forEach(askCityModal, function (item) {
			let acceptBtn = item.querySelector('.js_accept-city');
			let openCitiesBtn = item.querySelector('.js_open-cities');
			let openCitiesMobBtn = item.querySelector('.js_open-cities_mob');
			let closeBtn = item.querySelector('.ask-city__close');

			setTimeout(function () {
				item.classList.add('show')
			}, 2500);

			acceptBtn.addEventListener('click', function () {
				setCookie('location', '-');
				item.classList.remove('show')
			});
			closeBtn.addEventListener('click', function () {
				item.classList.remove('show')
			});
			if (openCitiesBtn) {
				openCitiesBtn.addEventListener('click', function () {
					item.classList.remove('show');
					setTimeout(function () {
						item.parentNode.querySelector('#dropdownSityMenuButton').click();
					}, 100);
				});
			}
			if (openCitiesMobBtn) {
				openCitiesMobBtn.addEventListener('click', function () {
					item.classList.remove('show');
					setTimeout(function () {
						document.querySelector('.header-icons__toggle').click();
					}, 200);
					setTimeout(function () {
						document.querySelector('#mainMenuMobileModal #dropdownSityMenuButton').click();
					}, 600);
				});
			}
		})
	}
})();

