import {Swiper, Pagination, Navigation} from "swiper/js/swiper.esm";
import {forEach, Move} from "../../js/helpers";
import {break_sm_up, break_md_up, break_lg_up, break_xl_up, break_xxl_up, break_xl_down} from "../../js/constants";

Swiper.use([Pagination, Navigation]);

(function () {
  const slider = document.querySelector('.b-reviews__slider');
  if (!slider) return;
  const wrapper = slider.parentNode;
  new Swiper(slider, {
      speed: 500,
      slidesPerView: 1,
      pagination: {
        el: wrapper.querySelector('.swiper-pagination'),
        type: 'bullets',
        clickable: true,
      },
      navigation: {
        nextEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-next'),
        prevEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-prev'),
      },
      breakpoints: {
        [break_xl_up]: {
          // slidesOffsetBefore: 80,
          shortSwipes: false,
          slidesPerView: 2,
        },
      },
      on: {
        init: function () {
          setTimeout(function () {
            slider.style.height = slider.offsetHeight + 'px';
          }, 500)
        },
      },
    }
  );
})();

