import {Swiper, Pagination, Navigation} from "swiper/js/swiper.esm";
import {break_sm_up, break_md_up, break_lg_up, break_xl_up, break_xxl_up, break_lg_down} from "../../js/constants";
import {forEach} from "../../js/helpers";

Swiper.use([Pagination, Navigation]);

forEach(document.querySelectorAll('.b-items__slider_shops'), slider => {
  const wrapper = slider.closest('.section');
  const slidesPerView = slider.dataset.slidesperview ? JSON.parse(slider.dataset.slidesperview) : [2, 3, 4, 5, 6, 6]

  new Swiper(slider, {
      speed: 500,
      slidesPerView: slidesPerView[0],
      pagination: {
        el: wrapper.querySelector('.swiper-pagination'),
        type: 'bullets',
        clickable: true,
      },
      navigation: {
        nextEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-next'),
        prevEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-prev'),
      },
      breakpoints: {
        [break_sm_up]: {
          slidesPerView: slidesPerView[1],
        },
        [break_md_up]: {
          slidesPerView: slidesPerView[2],
        },
        [break_lg_up]: {
          slidesPerView: slidesPerView[3],
        },
        [break_xl_up]: {
          // slidesOffsetBefore: 80,
          slidesPerView: slidesPerView[4],
        },
        [break_xxl_up]: {
          slidesPerView: slidesPerView[5],
        },
      },
    }
  );
});

