export function swiperProgress() {
  const swiper = this;
  for (var i = 0; i < swiper.slides.length; i++) {
    var slideProgress = swiper.slides[i].progress;
    var innerOffset = swiper.width;
    var innerTranslate = slideProgress * innerOffset;
    swiper.slides[i].querySelector(".slide-inner").style.transform =
      "translate3d(" + innerTranslate + "px, 0, 0)";
  }
}

//  cookie: setCookie('user', 'John', {secure: true, 'max-age': 3600});
export function setCookie(name, value, options = {}) {

  options = {
    path: '/',
    ...options
  };

  if (options.expires instanceof Date) {
    options.expires = options.expires.toUTCString();
  }

  let updatedCookie = encodeURIComponent(name) + "=" + encodeURIComponent(value);

  for (let optionKey in options) {
    updatedCookie += "; " + optionKey;
    let optionValue = options[optionKey];
    if (optionValue !== true) {
      updatedCookie += "=" + optionValue;
    }
  }

  document.cookie = updatedCookie;
}

//     name,
//  undefined,    
export function getCookie(name) {
  let matches = document.cookie.match(new RegExp(
      "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
  ));
  return matches ? decodeURIComponent(matches[1]) : undefined;
}

//  cookie  name
export function deleteCookie(name) {
  setCookie(name, "", {
    'max-age': -1
  })
}

export function debounce(f, ms) {
  let isCooldown = false;
  return function() {
    if (isCooldown) return;
    f.apply(this, arguments);
    isCooldown = true;
    setTimeout(() => isCooldown = false, ms);
  };
}

//     
export function filterByValue(array, string) {
  return array.filter(o => {
    return Object.keys(o).some(k => {
      if (typeof o[k] === 'string') return o[k].toLowerCase().includes(string.toLowerCase());
    });
  });
}