import {Swiper, Navigation} from "swiper/js/swiper.esm";
import {forEach} from "../../js/helpers";
import {break_lg_up, break_sm_up, break_xl_up, break_xxl_up} from "../../js/constants";

Swiper.use([Navigation]);

(function () {
  const slider = document.querySelector('.services__slider');
  if (!slider) return;
  new Swiper(slider, {
      speed: 500,
      watchOverflow: true,
      pagination: {
        el: slider.parentNode.querySelector('.swiper-pagination'),
        type: 'bullets',
        clickable: true,
      },
      breakpoints: {
        [break_sm_up]: {
          slidesPerView: 2,
        },
        [break_lg_up]: {
          slidesPerView: 3,
        },
        [break_xl_up]: {
          slidesPerView: 5,
        },
        [break_xxl_up]: {
          slidesPerView: 5,
        },
      },
    }
  );
})();
