import {Swiper, Pagination, Navigation} from "swiper/js/swiper.esm";
import {break_sm_up, break_xl_up} from "../../js/constants";

Swiper.use([Pagination, Navigation]);

(function () {
  const slider = document.querySelector('.info-card__slider');
  if (!slider) return;
  const wrapper = slider.closest('.section');
  new Swiper(slider, {
      speed: 500,
      loop: true,
      // slidesPerView: window.innerWidth/wrapper.offsetWidth,
      slidesPerView: 1.07,
      pagination: {
        el: wrapper.querySelector('.swiper-pagination'),
        type: 'bullets',
        clickable: true,
      },
      navigation: {
        nextEl: wrapper.querySelector('.swiper-button-next'),
        prevEl: wrapper.querySelector('.swiper-button-prev'),
      },
      breakpoints: {
        [break_sm_up]: {
          slidesPerView: 1.5,
        },
        [break_xl_up]: {
          slidesPerView: 2.15,
        },
      },
      on: {
        init() {
          const _this = this
          ;[].forEach.call(_this.$el[0].querySelectorAll('.info-card__content'), content => {
            const contentHidden = content.querySelector('.info-card__contentHide');
            contentHidden.style.setProperty('--max-height', contentHidden.scrollHeight + 'px');
            content.addEventListener('click', () => {
              ;[].forEach.call(_this.$el[0].querySelectorAll('.info-card__content'), content => content.classList.add('is-active'))
            }, true)
          });
        }
      }
    }
  );
})();


