import {Swiper, Pagination, Navigation} from "swiper/js/swiper.esm";
import {break_sm_up, break_md_up, break_lg_up, break_xl_up, break_xxl_up, break_lg_down} from "../../js/constants";
import {forEach, scrollToTop} from "../../js/helpers";

Swiper.use([Pagination, Navigation]);

(function () {
	const compareProdSliderEl = document.querySelector('.b-items__slider_compare');
	if (!compareProdSliderEl) return;
	const wrapper = compareProdSliderEl.closest('.section');
	const slidesPerView = compareProdSliderEl.dataset.slidesperview ? JSON.parse(compareProdSliderEl.dataset.slidesperview) : [2, 2, 3, 4, 5, 6]

	//    
	let compareProdSlider = new Swiper(compareProdSliderEl, {
				speed: 500,
				slidesPerView: slidesPerView[0],
				navigation: {
					nextEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-next'),
					prevEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-prev'),
				},
				breakpoints: {
					[break_sm_up]: {
						// shortSwipes: false,
						slidesPerView: slidesPerView[1],
					},
					[break_md_up]: {
						slidesPerView: slidesPerView[2],
					},
					[break_lg_up]: {
						slidesPerView: slidesPerView[3],
					},
					[break_xl_up]: {
						// slidesOffsetBefore: 80,
						slidesPerView: slidesPerView[4],
					},
					[break_xxl_up]: {
						slidesPerView: slidesPerView[5],
					},
				},
				on: {
					init: function () {
						const img = compareProdSliderEl.querySelector('.item__img img')

						function cb() {
							if (img.complete) {
								setTimeout(function () {
									compareProdSliderEl.style.height = compareProdSliderEl.offsetHeight + 'px';
								}, 1000)
							} else {
								setTimeout(function () {
									cb()
								}, 1000)
							}
						}

						cb()
					},
					setTranslate: function (a) {
						let css = this.$wrapperEl[0].style.transform;

						setTimeout(function () {
							forEach(document.querySelectorAll('.compare-block__slider'), (item) => {
								item.swiper.$wrapperEl[0].style.transform = css;
							})
						}, 0.001)
					},
					setTransition: function (a) {
						let trs = this.getTranslate();

						forEach(document.querySelectorAll('.compare-block__slider'), (item) => {
							item.swiper.$wrapperEl[0].style.transition = a + 'ms ease'
						})
					}
				},
			}
	);

	//    
	forEach(document.querySelectorAll('.compare-block__slider'), (slider, index, sliderArray) => {
		const wrapper = slider.closest('.section');
		const slidesPerView = slider.dataset.slidesperview ? JSON.parse(slider.dataset.slidesperview) : [2, 2, 3, 4, 5, 6]

		new Swiper(slider, {
					speed: 500,
					slidesPerView: slidesPerView[0],
					navigation: {
						nextEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-next'),
						prevEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-prev'),
					},
					breakpoints: {
						[break_sm_up]: {
							// shortSwipes: false,
							slidesPerView: slidesPerView[1],
						},
						[break_md_up]: {
							slidesPerView: slidesPerView[2],
						},
						[break_lg_up]: {
							slidesPerView: slidesPerView[3],
						},
						[break_xl_up]: {
							// slidesOffsetBefore: 80,
							slidesPerView: slidesPerView[4],
						},
						[break_xxl_up]: {
							slidesPerView: slidesPerView[5],
						},
					},
					on: {
						setTranslate: function (a) {
							let css = this.$wrapperEl[0].style.transform;
							compareProdSlider.$wrapperEl[0].style.transform = css;

							forEach(sliderArray, (item) => {
								item.swiper.$wrapperEl[0].style.transform = css;
							})
						},
						setTransition: function (a) {
							let trs = this.getTranslate();
							compareProdSlider.setTranslate(trs);
							compareProdSlider.$wrapperEl[0].style.transition = a + 'ms ease'

							forEach(sliderArray, (item) => {
								item.swiper.$wrapperEl[0].style.transition = a + 'ms ease'
							})
						}
					},
				}
		);

		slider.swiper.controller.control = compareProdSlider;
	});



	document.addEventListener("DOMContentLoaded", function () {
		const compareProdsSlider = document.querySelector('.b-items_compare');
		const compareProds = compareProdsSlider.querySelectorAll('.item');
		const compareSliderHeight = document.querySelector('.b-items__slider_compare').offsetHeight - 20;
		let scrollPos = 0;

		if (compareProdsSlider) {
			scrollToTop(0);
		}

		//     
		document.addEventListener('scroll', () => {
			let scrollTop = window.pageYOffset || document.documentElement.scrollTop;
			scrollPos = (document.body.getBoundingClientRect()).top;

			if (window.innerWidth > 768) {
				if (compareProdsSlider && scrollTop - 400 > compareProdsSlider.offsetTop) {

					compareProdsSlider.classList.add('minimized');
					compareProdsSlider.style.paddingTop = compareSliderHeight - 150 + 'px';
					forEach(compareProds, (item) => {
						item.style.height = '190px';
					})
				} else {

					compareProdsSlider.classList.remove('minimized');
					compareProdsSlider.style.paddingTop = '0';
					forEach(compareProds, (item) => {
						item.style.height = compareSliderHeight + 'px';
					})

				}
			} else {
				if (compareProdsSlider && scrollTop - 300 > compareProdsSlider.offsetTop) {

					compareProdsSlider.classList.add('minimized');
					compareProdsSlider.style.paddingTop = compareSliderHeight + 20 + 'px';
					forEach(compareProds, (item) => {
						item.style.height = '145px';
					})
				} else {
					compareProdsSlider.classList.remove('minimized');
					compareProdsSlider.style.paddingTop = '0px';
					forEach(compareProds, (item) => {
						item.style.height = compareSliderHeight + 'px';
					})
				}
			}
		})
	});
})();