import {Swiper, Controller, Navigation, Thumbs} from "swiper/js/swiper.esm";
import {forEach} from "../../js/helpers";
import {break_lg_up, break_md_up, break_sm_up, break_xl_up, break_xxl_up} from "../../js/constants";

Swiper.use([Controller, Navigation, Thumbs]);

//      ( + thumbs)
forEach(document.querySelectorAll('.commerce-info__interior-slider-wrap'), slider => {
	const thumbs = slider.querySelectorAll('.commerce-info__thumb');

	if (thumbs.length <= 5) {
		slider.querySelector('.commerce-info__interior-thumbs').classList.add('no-arrows')
	}

	const thumbsSlider = new Swiper(slider.querySelector('.commerce-info__interior-thumbs'), {
				speed: 500,
				slidesPerView: 5,
				navigation: {
					nextEl: '.swiper-button-next',
					prevEl: '.swiper-button-prev',
				},
				// loop: true
			}
	);

	const topSlider = new Swiper(slider.querySelector('.commerce-info__interior-slider'), {
				speed: 500,
				slidesPerView: 1,
				pagination: {
					el: slider.querySelector('.commerce-info__interior-slider .swiper-pagination'),
					type: 'bullets',
					clickable: true,
				},
				thumbs: {
					swiper: thumbsSlider
				},
			}
	);
});
forEach(document.querySelectorAll('.commerce-info__assorment-slider'), slider => {
	const wrapper = slider.closest('.section');
	const slidesPerView = slider.dataset.slidesperview ? JSON.parse(slider.dataset.slidesperview) : [1.1, 2, 2.5, 3, 3, 4]

	new Swiper(slider, {
				speed: 500,
				slidesPerView: slidesPerView[0],
				pagination: {
					el: wrapper.querySelector('.swiper-pagination'),
					type: 'bullets',
					clickable: true,
				},
				navigation: {
					nextEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-next'),
					prevEl: wrapper.querySelector('.swiper-navigation-parent .swiper-button-prev'),
				},
				breakpoints: {
					[break_sm_up]: {
						// shortSwipes: false,
						slidesPerView: slidesPerView[1],
					},
					[break_md_up]: {
						slidesPerView: slidesPerView[2],
					},
					[break_lg_up]: {
						slidesPerView: slidesPerView[3],
					},
					[break_xl_up]: {
						// slidesOffsetBefore: 80,
						slidesPerView: slidesPerView[4],
					},
					[break_xxl_up]: {
						slidesPerView: slidesPerView[5],
					},
				},
			}
	);
});
