import {Swiper, Controller, Navigation, Thumbs} from "swiper/js/swiper.esm";
import {forEach} from "../../js/helpers";
import {break_sm_up, break_xxl_up} from "../../js/constants";

Swiper.use([Controller, Navigation, Thumbs]);

forEach(document.querySelectorAll('.js-item-list'), item => {
  const sliderElem = item.querySelector('.item__slider')
  const thumbsElem = item.querySelector('.item__thumbsSlider')
  if (!thumbsElem) return;
  setTimeout(() => {
    const thumbs = new Swiper(thumbsElem, {
      direction: 'vertical',
      slidesPerView: 'auto',
      freeMode: true,
      watchSlidesVisibility: true,
      watchSlidesProgress: true,
      allowTouchMove: false,
      navigation: {
        nextEl: thumbsElem.parentNode.querySelector('.slider-button-next'),
        prevEl: thumbsElem.parentNode.querySelector('.slider-button-prev'),
      },
    });
    new Swiper(sliderElem, {
      navigation: {
        nextEl: sliderElem.querySelector('.swiper-button-next'),
        prevEl: sliderElem.querySelector('.swiper-button-prev'),
      },
      breakpoints: {},
      thumbs: {
        swiper: thumbs
      }
    })
  }, 2000)
  item.addEventListener('mouseenter', () => {
    item.classList.add('is-hover')
  })
  item.addEventListener('mouseleave', () => {
    setTimeout(() => {
      item.classList.remove('is-hover')
    }, 300)
  })
});
forEach(document.querySelectorAll('.js-item-single'), item => {
  const sliderElem = item.querySelector('.item__slider')
  new Swiper(sliderElem, {
    loop: true,
    navigation: {
      nextEl: sliderElem.querySelector('.swiper-button-next'),
      prevEl: sliderElem.querySelector('.swiper-button-prev'),
    },
    breakpoints: {},
  })
});

forEach(document.querySelectorAll('[data-type="option"]'), item => {
  item.addEventListener('click', () => {
    if (!item.getAttribute('data-original-title-default')) {
      item.setAttribute('data-original-title-default', item.getAttribute('data-original-title'))
    }
    if(item.classList.contains('is-active')) {
      item.classList.remove('is-active')
      item.setAttribute('data-original-title', item.getAttribute('data-original-title-default'))
    } else {
      item.classList.add('is-active')
      item.setAttribute('data-original-title', item.getAttribute('data-active-title'))
    }
  })
});


