import {
  Swiper,
  Pagination,
  Navigation,
  EffectFade,
  EffectCoverflow,
  Autoplay,
  Thumbs,
  Controller
} from "swiper/js/swiper.esm";
import {break_md_up} from "../../js/constants";
import {swiperProgress} from "../../js/helpers";

Swiper.use([Controller, Pagination, Navigation, EffectFade, EffectCoverflow, Autoplay, Thumbs]);

(function () {

  const sliderItemEl = document.querySelector('.sliderItem');
  if (!sliderItemEl) return;

  const sliderItem = new Swiper(sliderItemEl, {
    loop: true,
    speed: 1500,
    grabCursor: false,
    watchSlidesProgress: true,
    mousewheelControl: false,
    keyboardControl: false,
    breakpoints: {
      [break_md_up]: {
        speed: 1500,
      }
    },
    on: {
      progress: swiperProgress,
      touchStart: function() {
        const swiper = this;
        for (var i = 0; i < swiper.slides.length; i++) {
          swiper.slides[i].style.transition = "";
        }
      },
      setTransition: function(speed) {
        const swiper = this;
        for (var i = 0; i < swiper.slides.length; i++) {
          swiper.slides[i].style.transitionDuration = speed + "ms";
          swiper.slides[i].querySelector(".slide-inner").style.transitionDuration = speed + "ms";
        }
      },
    }
  })

  const sliderMain = document.querySelector('.sliderMain__inner');
  if (!sliderMain) return;
  const wrapper = sliderMain.parentNode;
  const pagination = wrapper.querySelector('.swiper-pagination');
  const delay = 4000;
  pagination.style.setProperty('--delay', delay + 'ms');
  const Slider = new Swiper(sliderMain, {
    loop: true,
    speed: 1500,
    grabCursor: true,
    watchSlidesProgress: true,
    mousewheelControl: true,
    keyboardControl: true,
    autoplay: {
      delay: delay,
    },
    navigation: {
      nextEl: wrapper.querySelector('.swiper-button-next'),
      prevEl: wrapper.querySelector('.swiper-button-prev'),
    },
    pagination: {
      el: pagination,
      type: 'bullets',
      clickable: true,
    },
    breakpoints: {
      [break_md_up]: {
        speed: 1500,
        pagination: {
          el: pagination,
          type: 'progressbar',
          clickable: true,
        },
      },
    },
    on: {
      init: function() {
        const swiper = this;
        setTimeout(function () {
          swiper.pagination.$el.children()[0].classList.add('tr-none')
          setTimeout(function () {
            swiper.pagination.$el.children()[0].classList.remove('tr-none')
          }, 10)
        }, 10)
      },
      progress: function() {
        const swiper = this;
        if(swiper.progress === 1) {
          swiper.pagination.$el.children()[0].classList.add('tr-none')
          setTimeout(function () {
            swiper.pagination.$el.children()[0].classList.remove('tr-none')
          }, 10)
        }
        for (var i = 0; i < swiper.slides.length; i++) {
          var slideProgress = swiper.slides[i].progress;
          var innerOffset = swiper.width;
          var innerTranslate = slideProgress * innerOffset;
          swiper.slides[i].querySelector(".slide-inner").style.transform =
            "translate3d(" + innerTranslate + "px, 0, 0)";
        }
      },
      touchStart: function() {
        const swiper = this;
        for (var i = 0; i < swiper.slides.length; i++) {
          swiper.slides[i].style.transition = "";
        }
      },
      setTransition: function(speed) {
        const swiper = this;
        for (var i = 0; i < swiper.slides.length; i++) {
          swiper.slides[i].style.transitionDuration = speed + "ms";
          swiper.slides[i].querySelector(".slide-inner").style.transitionDuration = speed + "ms";
        }
      },
    }
  });

  sliderItem.controller.control = Slider;
  Slider.controller.control = sliderItem;

  sliderItemEl.addEventListener('mouseenter', () => {
    Slider.autoplay.stop();
    // console.log(1)
  });
  sliderItemEl.addEventListener('mouseleave', () => {
    Slider.autoplay.start();
    // console.log(2)
  });


})();
