import {forEach, Move} from "../../js/helpers";
import {break_lg_down} from "../../js/constants";

// new Move(document.querySelector('[data-move="footerSocialHash-get"]'), document.querySelector('[data-move="footerSocialHash-set"]'), break_lg_down).init();
forEach(document.querySelectorAll('.nav-footer'), nav => {
  const toggle = nav.querySelector('.nav-footer__toggle')
  if (!toggle) return;
  toggle.addEventListener('click', () => {
    nav.classList.add('is-active')
  })
});
