import {forEach} from "../../js/helpers";

forEach(document.querySelectorAll('[data-toggle="filter"]'), link => {
  link.addEventListener('click', () => {
    document.body.classList.toggle('is-filter-open')
  })
});

forEach(document.querySelectorAll('.filter-color .form-check-input'), checkbox => {
  let checkParent = checkbox.parentNode;

  checkbox.addEventListener('change', () => {
    if (checkbox.checked) {
      checkParent.style.zIndex = 2;
    } else {
      checkParent.style.zIndex = 1;
    }
  })
});
