forEach(document.querySelectorAll('.mainNav__item.is-parent'), item => {
  const link = item.children[0]
  const targetEl = document.querySelector(link.dataset.href)
  const inner = targetEl.querySelector('.mainNav-inner')
  let prevCollapsed;
  if(window.innerWidth <= break_xl_down) {
    const initCollapse = (link, targetEl) => {
      const myCollapse = new bootstrap.Collapse(targetEl)
      setTimeout(function () {
        myCollapse.hide()
      }, 1000)
      link.addEventListener('click', e => {
        e.preventDefault()
        link.classList.toggle('show')
        myCollapse.toggle()
        prevCollapsed = myCollapse
      })
    }
    initCollapse(link, targetEl)
    console.log(targetEl)
    forEach(targetEl.querySelectorAll('.has-dropdown'), link => {
      link.classList.add('dropdown-toggle')
      link.nextElementSibling.classList.add('collapse')
      initCollapse(link, link.nextElementSibling)
    })
  } else {
    targetEl.classList.add('modal')
    var myModal = new bootstrap.Modal(targetEl, {
      backdrop: false
    })
    link.addEventListener('click', e => {
      e.preventDefault()
      if (item.classList.contains('is-active')) {
        item.classList.remove('is-active')
        myModal.hide()
      } else {
        item.classList.add('is-active')
        targetEl.style.setProperty('--left', link.offsetLeft + 'px')
        targetEl.style.setProperty('--top', (link.offsetTop + link.offsetHeight) + 'px')
        targetEl.querySelector('.modal-dialog').style.paddingLeft = link.offsetLeft + 'px'
        if (!inner.dataset.paddingTop) {inner.dataset.paddingTop = parseInt(getComputedStyle(inner).paddingTop)}
        myModal.show()
      }
    })
    document.addEventListener('click', e => {
      if(e.target.closest('.mainNav-inner')) return;
      if(e.target === link) return;
      item.classList.remove('is-active')
      myModal.hide()
    })
  }
})
